
.faqSection {
  padding: em(150) 0;
  position: relative;
  overflow: hidden;
  background: var(--navyBlueDark);
  h3, h6 {
    color: var(--gold);
  }
  h3 {
    margin-bottom: em(30);
  }
  .videoBg {
    background: var(--navyBlueDark);

    &:after {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0.5;
      background: linear-gradient(180deg, #00133F 0%, #000218 100%);
      content: '';
    }
    &:before {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0.5;
      background: linear-gradient(180deg, #000218 0%, #00133F 100%);
      content: '';
    }
    video {
      mix-blend-mode: color-dodge;
    }
  }
  .container-fluid,
  .container-s {
    position: relative;
    z-index: 2;
  }
  @media #{$ipadL} and #{$ipadpro} {
    padding: em(80) 0;
  }
  @media #{$ipad}{
    padding: em(80) 0;
  }
  @media #{$mobL} {
    padding: em(50) 0;
  }
}

.videoBgOverly {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(102.15% 61.85% at 26.18% 61.99%, rgba(0, 19, 64, 0.00) 0%, rgba(1, 15, 48, 0.90) 100%);
}

.faqAccordions {
  padding-top: em(80);
  @media #{$mobL} {
    padding-top: em(50);
  }
}

.accordionItem {
  border-bottom: solid 1px var(--lightGold);
  + .accordionItem {
    margin-top: em(50);
  }
  &.active {
    .accordionTitle {
      i {
        &:after {
          transform: rotate(90deg);
        }
      }
    }
    .accordionContent {
      display: block;
    }
  }
  @media #{$mobL} {
    + .accordionItem {
      margin-top: em(30);
    }
  }
}

.accordionTitle {
  color: var(--lightGold);
  padding: 0 em(80) em(20) 0;
  position: relative;
  cursor: pointer;
  span {
    font-size: em(22);
    line-height: 1.3;
  }
  i {
    position: absolute;
    right: 0;
    top: em(5);
    width: em(26);
    height: em(26);
    @include borderRadius(50%);
    background: var(--lightGold);
    &:before,
    &:after {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      background: var(--navyBlue);
      @include transitions(0.5s);
      content: '';
    }
    &:before {
      width: em(14);
      height: 2px;
    }
    &:after {
      height: em(14);
      width: 2px;
    }
  }
}

.accordionContent {
  display: none;
}