.ourLocationSection{
  padding: em(125) 0;
  background: var(--lightGold);
  overflow: hidden;
  @media #{$ipadL} and #{$ipadpro} {
    padding: em(80) 0;
  }
  @media #{$ipad}{
    padding: em(80) 0;
  }
  @media #{$mobL} {
    padding: em(50) 0;
  }
}

.ourLocationHeading{
  h3{
    margin: 0;
  }

  @media #{$mobL} {
    margin-bottom: em(20);
  }
}

.mapTabs{
  display: flex;
  justify-content: flex-end;
  ul{
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    li{
      list-style: none;
      position: relative;
      margin: 0;
      a{
        display: block;
        padding: em(12) em(25);
        color: var(--navyBlue);
        border: solid 1px var(--navyBlue);
        font-size: em(18);
        border-radius: 100px;
        font-family: $basefont;
        &:hover{
          text-decoration: none;
        }
      }
      &:first-of-type{
        left: em(10);
      }
      &.active{
        z-index: 1;
        a{
          background: var(--navyBlue);
          color: var(--lightGold);
        }
      }
    }
  }
  @media #{$mobL} {
    display: block;
    ul{
      justify-content: space-between;
      li{
        &:first-of-type{
          left: initial;
        }
        a{
          font-size: em(16);
        }
      }
    }
  }
}

.mapTabWrapper{
  padding: em(40) 0 0 0;
}

