.relatedNewsSection{
  background: var(--lightGold);
  padding: em(120) 0;
  overflow: hidden;
  position: relative;
  .newsListing {
    margin-top: em(50);
  }
  @media #{$desktop} {
    .newsListing {
      .newsCard{
        margin-bottom: 0;
      }
    }
  }

  @media #{$ipadL} and #{$ipadpro} {
    padding: em(80) 0;
  }
  @media #{$ipad}{
    padding: em(80) 0;
    .newsListing {
      .newsCard{
        &:last-of-type{
          margin-bottom: 0;
        }
      }
    }
  }
  @media #{$mobL} {
    padding: em(50) 0;
  }
}

.blueColor{
  color: var(--navyBlue);
}