.innerHeroBanner {
  position: relative;
  width: 100%;
  height: em(750);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: em(70) 0;
  z-index: 2;
  .container-s,
  .container-fluid {
    position: relative;
    z-index: 2;
  }
  color: var(--lightGold);

  @media #{$ipadL} and #{$ipadpro} {
    height: em(620);
  }

  @media #{$ipad} {
    height: em(550);
  }

  @media #{$mobL} {
    height: em(400);
    padding: em(50) 0;
  }
}

.innerHeroBannerBg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  &:before, &:after {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    content: '';
  }
  &:before {
    background: linear-gradient(180deg, #000014 -31.82%, rgba(0, 0, 20, 0.00) 100%);
  }
  &:after {
    background: linear-gradient(180deg, #000014 -31.82%, rgba(0, 0, 20, 0.00) 100%);
  }
  video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    aspect-ratio: 16/9;
  }
}

.bgMask {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.2;
  background: #3B60A0;
  mix-blend-mode: screen;
  filter: blur(125px);
}