.titleHeading {
  color: var(--lightGold);
}

.newsDateAndCity {
  color: var(--lightGold);
  display: flex;
  justify-content: center;
  align-items: center;
  span {
    font-size: em(22);
    position: relative;
    padding: 0 em(50);
    line-height: 1;

    &:before {
      position: absolute;
      right: 0;
      top: 0;
      width: 1px;
      height: 100%;
      background: var(--lightGold);
      content: '';
    }

    &:first-of-type {
      padding-left: 0;
    }
    &:last-of-type {
      padding-right: 0;
      &:before {
        display: none;
      }
    }
  }

  @media #{$mobL}{
    margin-top: em(10);
    span{
      padding: 0 em(20);
      font-size: em(14);
      &:first-of-type {
        padding-left: 0;
      }
      &:last-of-type {
        padding-right: 0;
        &:before {
          display: none;
        }
      }
    }
  }
}

.detailContent{
  padding: em(40) 0;
  &:first-of-type{
    padding-top: em(80);
  }
  &:last-child{
    padding-bottom: 0;
  }


  @media #{$ipadL} and #{$ipadpro} {
    padding: em(30) 0;
    &:first-of-type{
      padding-top: em(50);
    }
    &:last-child{
      padding-bottom: 0;
    }
  }
  @media #{$ipad} {
    padding: em(30) 0;
    &:first-of-type{
      padding-top: em(50);
    }
    &:last-child{
      padding-bottom: 0;
    }
  }
  @media #{$mobL}{
    padding: em(30) 0;
    &:first-of-type{
      padding-top: em(50);
    }
    &:last-child{
      padding-bottom: 0;
    }
  }
}

.dtlImag{
  height: em(700);
  overflow: hidden;
  margin: 0 0 em(80) 0;
  &:last-of-type{
    margin-bottom: 0;
  }
  @media #{$ipadL} and #{$ipadpro} {
    height: em(550);
    margin: 0 0 em(50) 0;
  }
  @media #{$ipad} {
    height: em(450);
    margin: 0 0 em(50) 0;
  }
  @media #{$mobL}{
    height: em(350);
    margin: 0 0 em(30) 0;
  }
}

.dtlTxt{
  width: 80%;
  margin: auto;
  color: var(--white);
  p{
    font-size: em(18);
    margin-bottom: em(20);
    line-height: 1.3;
    letter-spacing: 1px;
    &:last-child{
      margin-bottom: 0;
    }
  }
  @media #{$ipad} {
    width: 100%;
  }
  @media #{$mobL}{
    width: 100%;
    p{
      font-size: em(16);
    }
  }
}

.playBtn{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  i{
    width: em(100);
    height: em(100);
    display: block;
    img{
      width: 100%;
      height: 100%;
    }
  }
}