.getInTouchSection {
  position: relative;
  overflow: hidden;
  background: var(--navyBlue);
  padding: em(130) 0;
  .videoBg {
    &:after {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0.85;
      background: linear-gradient(180deg, #000218 0%, #00133F 100%);
      content: '';
    }
  }
  .container-fluid,
  .container-s {
    position: relative;
    z-index: 2;
  }
  @media #{$ipadL} and #{$ipadpro} {
    padding: em(80) 0;
  }
  @media #{$ipad}{
    padding: em(80) 0;
  }
  @media #{$mobL} {
    padding: em(50) 0;
  }
}

.locationContent {
  p {
    margin-bottom: em(20);
  }
  + .locationContent {
    margin-top: em(100);
  }
  @media #{$ipadL} and #{$ipadpro} {
    + .locationContent {
      margin-top: em(80);
    }
    p {
      font-size: em(18);
    }
  }
  @media #{$ipad} {
    margin-top: em(60);
    padding-top: em(60);
    border-top: solid 1px var(--lightGold);
    + .locationContent {
      margin-top: em(60);
    }
    p {
      font-size: em(18);
    }
  }
  @media #{$mobL} {
    margin-top: em(50);
    padding-top: em(50);
    border-top: solid 1px var(--lightGold);
    + .locationContent {
      margin-top: em(50);
    }
  }
}

.locHeading {
  font-size: em(28);
  line-height: 1;
  margin: 0 0 em(20) 0;
  color: var(--lightGold);
  @media #{$ipadL} and #{$ipadpro} {
    font-size: em(24);
  }
  @media #{$ipad} {
    font-size: em(24);
  }
}

.contactInfo {
  margin: 0 0 em(30) 0;
  padding: 0;
  li {
    list-style: none;
    a {
      font-size: em(22);
      line-height: 1;
      color: var(--white);
      font-family: $arboriaBook;
      @include transitions(0.5s);
    }
  }
  @media #{$ipadL} and #{$ipadpro} {
    li {
      a {
        font-size: em(18);
      }
    }
  }
  @media #{$ipad} {
    li {
      a {
        font-size: em(18);
      }
    }
  }
}

.getInTouchForm {
  .form-group {
    margin: 0 0 0 0;
  }
}

@media (hover: hover) {
  .contactInfo {
    li {
      a {
        &:hover {
          color: var(--gold);
        }
      }
    }
  }
}