.newsAndMedia {
  padding: em(150) 0;
  position: relative;
  overflow: hidden;
  background: var(--navyBlueDark);
  .videoBg {
    background: var(--navyBlueDark);
    position: fixed;
    top: 0;
    left: 0;
    &:after {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0.5;
      background: linear-gradient(180deg, #00133F 0%, #000218 100%);
      content: '';
    }
    &:before {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0.5;
      background: linear-gradient(180deg, #000218 0%, #00133F 100%);
      content: '';
    }
    video {
      mix-blend-mode: color-dodge;
    }
  }
  .container-fluid,
  .container-s {
    position: relative;
    z-index: 2;
  }
  @media #{$ipadL} and #{$ipadpro} {
    padding: em(80) 0;
  }
  @media #{$ipad}{
    padding: em(80) 0;
  }
  @media #{$mobL} {
    padding: em(50) 0;
  }
}

.newsListing{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 0 em(30);
  .newsCard{
    width: 28.3%;
    margin-bottom: em(120);
  }
  @media #{$ipadL} and #{$ipadpro} {
    padding: 0;
    .newsCard{
      width: 30%;
      margin-bottom: em(80);
      .newCardText{
        padding: em(30);
        min-height: em(350);
      }
      .newsCardImg{
        height: em(220);
      }
    }
  }
  @media #{$ipad}{
    padding: 0;
    justify-content: space-between;
    .newsCard{
      width: 45%;
      margin-bottom: em(80);
      .newCardText{
        padding: em(30);
        min-height: em(350);
      }
      .newsCardImg{
        height: em(220);
      }
    }
  }
  @media #{$mobL} {
    display: block;
    padding: 0;
    .newsCard{
      width: 100%;
      margin-bottom: em(30);
    }
  }
}

.data-loading{
  position: relative;
  display: flex;
  justify-content: center;
}