.data-loading{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.loader {
  width: em(30);
  height: em(30);
  display: grid;
  border-radius: 50%;
  -webkit-mask: radial-gradient(farthest-side, #0000 40%, #000 41%);
  background: linear-gradient(0deg, #FFFFFF80 50%, #FFFFFFFF 0) center/2px 100%,
  linear-gradient(90deg, #FFFFFF40 50%, #FFFFFFBF 0) center/100% 2px;
  background-repeat: no-repeat;
  animation: s3 0.8s infinite steps(11);
}

.loader::before,
.loader::after {
  content: "";
  grid-area: 1/1;
  border-radius: 50%;
  background: inherit;
  opacity: 0.915;
  transform: rotate(30deg);
}

.loader::after {
  opacity: 0.83;
  transform: rotate(60deg);
}

@keyframes s3 {
  100% {
    transform: rotate(1turn)
  }
}

.loaderText {
  color: var(--white);
  font-size: em(22);
  line-height: 1;
  font-family: $arboriaBook;
  margin-top: em(10);
}