.discoverSection {
  position: relative;
  min-height: em(743);
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  padding: em(60) 0;
  .imageBg {
    &:before, &:after {
      position: absolute;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, #090A0E 0%, rgba(9, 10, 14, 0.00) 50.06%);
      mix-blend-mode: multiply;
      bottom: 0;
      content: '';
    }
    &:before {

    }
    &:after {
      transform: rotate(180deg);
    }
  }
  .container-fluid,
  .container-s {
    position: relative;
    z-index: 2;
  }

  @media #{$ipadL} and #{$ipadpro} {
    min-height: em(600);
  }

  @media #{$ipad} {
    min-height: em(500);
  }

  @media #{$mobL} {
    min-height: em(500);
  }
}

.discoverHeading {
  h3 {
    margin-bottom: 0;
  }

  @media #{$ipad} {
    margin-bottom: em(20);
  }

  @media #{$mobL} {
    margin-bottom: em(15);
  }
}

.discoverTxt {
  p {
    margin-bottom: em(20);
  }
}